@font-face {
  font-family: 'Forum';
  src: url('../fonts/forum.woff2') format('woff2'), url('../fonts/forum.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Ubuntu Mono';
  src: url('../fonts/ubuntumono.woff2') format('woff2'), url('../fonts/ubuntumono.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  margin: 0;
  width: 100%;
  max-width: 100%;
}
body {
  font-family: 'Ubuntu Mono', Courier, monospace;
  font-weight: 400;
  margin: 0;
  width: 100%;
  max-width: 100%;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.footer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 15vh;
  background-color: #3B181E;
}
.footer__email {
  font-size: 18px;
  font-weight: 400;
  color: #FFFFFF;
}
.hero {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 288px;
  min-height: 100vh;
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: 'Ubuntu Mono', Courier, monospace;
  font-weight: 400;
  min-width: 320px;
  background-color: #3B181E;
  color: #FFFFFF;
}
.name {
  text-align: center;
  width: 240px;
  margin-top: 0;
  margin-bottom: 110px;
  font-size: 36px;
  font-weight: 400;
}
.title {
  margin-top: 28px;
  margin-bottom: 82px;
  font-size: 18px;
  font-weight: 400;
}
.button {
  width: 177px;
  min-height: 50px;
  margin-bottom: 46px;
  background-color: #230E12;
  border: none;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.4);
  color: rgba(255, 255, 255, 0.8);
}
.social {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 26px;
}
.social li {
  margin-right: 40px;
}
.social li:last-child {
  margin-right: 0;
}
.social__link {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  font-size: 0;
}
.portfolio {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 288px;
  min-height: 85vh;
  position: relative;
}
.portfolio::before {
  content: "";
  position: absolute;
  width: 288px;
  height: 1px;
  top: 0;
  left: calc(50% - 288px / 2);
  background-color: rgba(255, 255, 255, 0.4);
}
.portfolio__title {
  margin-top: 30px;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 400;
}
.slider {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.slider__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 10px;
  padding-bottom: 15px;
}
.slider__list li:not(:last-child) {
  margin-right: 12px;
}
.slider__list::before {
  content: "";
  position: absolute;
  width: 288px;
  height: 1px;
  bottom: 0;
  left: calc(50% - 288px / 2);
  background-color: rgba(255, 255, 255, 0.4);
}
.slider__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.slider__list-wrapper {
  margin: 0;
  padding: 0;
  border: none;
}
.slider__item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 63px;
  height: 30px;
  font-size: 0;
}
.slider__label {
  position: relative;
  width: 63px;
  height: 30px;
}
.slider__label::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.4);
}
.slider__radio:checked ~ .slider__label::before {
  background-color: #FFFFFF;
}
.slider__link {
  width: 288px;
  height: 392px;
  background-position: 0 0;
  background-size: 288px;
  background-repeat: no-repeat;
}
.slider__link--kekstagram {
  background-image: url('../img/preview-1.jpg');
}
.slider__link--cat-energy {
  background-image: url('../img/preview-2.png');
}
.slider__link--technomart {
  background-image: url('../img/preview-3.jpg');
}
.slider__link--test-task {
  background-image: url('../img/preview-4.jpg');
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
